export default function(state = {}, action) {
    switch (action.type) {
      case "SAVE_USER":
        return {
          ...state,
          saveuser: action.payload
      };
      case "LOGIN":
        return {
          ...state,
          login: action.payload
      };
      case "GET_QUALIFICATION":
        return {
          ...state,
          getqualification: action.payload
      };
      case "GET_ALL_COMPANIES":
        return {
          ...state,
          getallcompanies: action.payload
      };
      case "GET_ALL_COUNTRIES":
        return {
          ...state,
          getallcountries: action.payload
      };
      case "GET_ALL_CITY_BY_COUNTRY":
        return {
          ...state,
          getallcitybycountry: action.payload
      };
      case "GET_ALL_USERS_BY_COMPANY":
        return {
          ...state,
          getallusersbycompany: action.payload
      };
      default:
        return state
    }
  }