import React from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
// import AppBar from '@material-ui/core/AppBar';
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
// import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Home from "@material-ui/icons/Home";
import Timeline from "@material-ui/icons/Timeline";
import AccountCircle from "@material-ui/icons/AccountCircle";
import Note from "@material-ui/icons/Note";
import MenuAppBar from "./AppBar";
import { withRouter, Link } from "react-router-dom";
const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBar: {
    backgroundColor: "#031926",
    color: "white",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    backgroundColor: "#031926",
    color: "white",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    backgroundColor: "#FAFAFA",
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap"
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    backgroundColor: "#031926",
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "2 8px",
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    marginLeft: -115
    // padding: theme.spacing(3),
    // marginTop: 80
  },
  list: {
    backgroundColor: "#031926",
    height: "100%"
  },
  listItem: {
    backgroundColor: "#031926"
  },
  text: {
    color: "white"
  },
  paper: {
    padding: theme.spacing(0, 0)
  },
  heading: {
    backgroundColor: "#19469C",
    color: "white",
    padding: 10
  },
  subicon: {
    backgroundColor: "#19469C",
    padding: 10,
    fontSize: 20,
    textAlign: "center",
    color: "white"
  },
  subiconText: {
    textDecoration: "none",
    color: "white",
    "&:hover": {
      color: "#339AF0",
      textDecoration: "none"
    }
  },
  typography: {
    padding: 20
  },
  news: {
    fontSize: 20,
    color: "black"
  },
  grid: {
    padding: 10,
    textAlign: "center"
  },
  hover: {
    "&:hover": {
      color: "#18469C",
      textDecoration: "none",
      textShadow: "5px 1px 12px #18469C"
    }
  },
  courseContainer: {
    backgroundColor: "#F8F6F6",
    textAlign: "left",
    marginBottom: 10
  },
  courseName: {
    fontSize: 30,
    textDecoration: "none"
  },
  courseLink: {
    color: "black",
    "&:hover": {
      color: "#18469C",
      textDecoration: "none",
      textShadow: "5px 1px 12px #18469C"
    }
  },
  white: {
    backgroundColor: "white",
    borderRadius: "100%",
    color: "black"
  }
}));

function DrawerView(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  function handleDrawerOpen() {
    setOpen(true);
  }

  function handleDrawerClose() {
    setOpen(false);
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <MenuAppBar
        forAppBar={clsx(classes.appBar, {
          [classes.appBarShift]: open
        })}
        forIconButton={clsx(classes.menuButton, {
          [classes.hide]: open
        })}
        OpenDrawer={handleDrawerOpen}
      />
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open
          })
        }}
        open={open}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon className={classes.white} />
            ) : (
              <ChevronLeftIcon className={classes.white} />
            )}
          </IconButton>
        </div>
        <List className={classes.list}>
          <Link className={classes.text} to="/">
            <ListItem button className={classes.listItem}>
              <ListItemIcon className={classes.text}>
                <Home />
              </ListItemIcon>
              <ListItemText className={classes.text} primary={"Home"} />
            </ListItem>
          </Link>
          <Link className={classes.text} to="/profile">
            <ListItem className={classes.listItem} button>
              <ListItemIcon className={classes.text}>
                <AccountCircle />
              </ListItemIcon>
              <ListItemText className={classes.text} primary={"My Profile"} />
            </ListItem>
          </Link>
          <ListItem className={classes.listItem} button>
            <ListItemIcon className={classes.text}>
              <Timeline />
            </ListItemIcon>
            <ListItemText
              className={classes.text}
              primary={"Progress Timeline"}
            />
          </ListItem>
          <ListItem className={classes.listItem} button>
            <ListItemIcon className={classes.text}>
              <Note />
            </ListItemIcon>
            <ListItemText className={classes.text} primary={"My Notes"} />
          </ListItem>
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <br />
        {props.children}
      </main>
    </div>
  );
}

export default withRouter(DrawerView);
