import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import { Link } from "react-router-dom";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import BackgroundImage from "../assets/background.jpg";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { login } from "../Actions/authActions";
import isEmpty from "./Protected/validation/is-empty";
const useStyles = makeStyles(theme => ({
  root: {
    height: "100vh"
  },
  image: {
    backgroundImage: `url(${BackgroundImage})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  error: {
    color: "red"
  }
}));

function Signin(props) {
  const classes = useStyles();
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [values, setValues] = React.useState({
    email: "",
    password: ""
  });
  const { login } = props;

  const handleChange = name => event => {
    setErrors({});
    setValues({ ...values, [name]: event.target.value });
  };

  const loginFunc = e => {
    e.preventDefault();
    const errors = {};
    // if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(values.email)) {
    if (!/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(values.email)) {
      errors.email = "Enter a valid email";
    }

    if (isEmpty(values.password)) {
      errors.password = "Password is Required";
    }

    setErrors(errors);

    if (isEmpty(errors)) {
      login(values, props.history);
      setLoading(true);
    }
  };

  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      <Grid item xs={false} sm={4} md={7} className={classes.image} />
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          {props.errorLogin === 404 ? (
            <Typography className={classes.error} component="h6" variant="h5">
              Enter Correct Email or Password
            </Typography>
          ) : null}
          <form className={classes.form} noValidate>
            <TextField
              error={!isEmpty(errors.email) ? true : false}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              helperText={errors.email ? errors.email : ""}
              label="Email"
              onChange={handleChange("email")}
              name="email"
              autoComplete="email"
            />
            <TextField
              error={!isEmpty(errors.password) ? true : false}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="password"
              helperText={errors.password ? errors.password : ""}
              label="Password"
              type="password"
              onChange={handleChange("password")}
              name="password"
              autoComplete="password"
            />
            {loading ? (
              props.errorLogin === 404 ? (
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                  onClick={loginFunc}
                >
                  Sign In
                </Button>
              ) : (
                <div className="text-center py-4">
                  <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
                </div>
              )
            ) : (
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={loginFunc}
              >
                Sign In
              </Button>
            )}

            <Grid container>
              <Grid item xs>
                <Link to="/forgotpassword" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link to="/signup" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
      </Grid>
    </Grid>
  );
}

const mapStateToProps = state => ({
  login: state.root.login,
  errorLogin: state.errors
});
export default withRouter(
  connect(
    mapStateToProps,
    { login }
  )(Signin)
);
