const TOKEN_KEY = 'admin';

export const a_login = (history) => {
    localStorage.setItem(TOKEN_KEY, 'AdminLogin');
    history.push("/adashboard")
}

export const a_logout = (history) => {
    localStorage.removeItem(TOKEN_KEY);
    history.push("/admin")
}

export const a_isLogin = () => {
    if (localStorage.getItem(TOKEN_KEY)) {
        return true;
    }
    return false;
}