import React from "react";
import { makeStyles } from "@material-ui/core/styles";
// import AppBar from '@material-ui/core/AppBar';
import Typography from "@material-ui/core/Typography";
import Timeline from "@material-ui/icons/Timeline";
import Subscriptions from "@material-ui/icons/Subscriptions";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { Link, withRouter } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faNewspaper,
  faLink,
  faPollH,
  faBookReader,
  faQuestion,
  faCommentDots,
  faGlobe
} from "@fortawesome/free-solid-svg-icons";
import DrawerView from "./DrawerView"
import { faReact, faAngular } from "@fortawesome/free-brands-svg-icons";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBar: {
    backgroundColor: "white",
    color: "#031926",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    backgroundColor: "white",
    color: "#031926",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap"
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    marginLeft: -100,
    padding: theme.spacing(3)
  },
  list: {
    backgroundColor: "#19469C",
    height: "100%"
  },
  listItem: {
    backgroundColor: "#19469C"
  },
  text: {
    color: "white"
  },
  paper: {
    padding: theme.spacing(0, 0)
  },
  heading: {
    backgroundColor: "#468189",
    color: "#E0FBFC",
    padding: 10
  },
  subicon: {
    backgroundColor: "#468189",
    padding: 10,
    fontSize: 20,
    textAlign: "center",
    color: "white"
  },
  subiconText: {
    textDecoration: "none",
    color: "white",
    "&:hover": {
      color: "#77ACA2",
      textDecoration: "none"
    }
  },
  typography: {
    padding: 20
  },
  news: {
    fontSize: 20,
    color: "#031926",
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  grid: {
    padding: 10,
    textAlign: "center"
  },
  hover: {
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  courseContainer: {
    backgroundColor: "#F8F6F6",
    textAlign: "left",
    marginBottom: 10
  },
  courseName: {
    fontSize: 30,
    [theme.breakpoints.down("sm")]: {
      fontSize: 16
    },
    textDecoration: "none"
  },
  courseLink: {
    color: "#031926",
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  container: {
    padding: 30
  }
}));

function Dashboard() {
  const classes = useStyles();

  return (
    <DrawerView>
      <Grid container spacing={2} className={classes.container}>
      <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
        <Paper className={classes.paper}>
          <Typography className={classes.heading} variant="h5" component="h3">
            My Current Courses{" "}
          </Typography>
          <Grid container spacing={1} className={classes.grid}>
            <Grid
              item
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              className={classes.courseContainer}
            >
              <Link to="/course/reactjs" className={classes.courseLink}>
                <Typography className={classes.courseName}>
                  &nbsp;&nbsp;
                  <FontAwesomeIcon icon={faReact} /> &nbsp; Reactjs
                </Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              className={classes.courseContainer}
            >
              <Link to="/course/freelancing" className={classes.courseLink}>
                <Typography className={classes.courseName}>
                  &nbsp;&nbsp;
                  <FontAwesomeIcon icon={faGlobe} /> &nbsp; Freelancing
                </Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              className={classes.courseContainer}
            >
              <Link to="/course/angular" className={classes.courseLink}>
                <Typography className={classes.courseName}>
                  &nbsp;&nbsp;
                  <FontAwesomeIcon icon={faAngular} /> &nbsp; Angular
                </Typography>
              </Link>
            </Grid>
          </Grid>
          <Typography className={classes.subicon} component="p">
            <Link to="/enroll" className={classes.subiconText}>
              <Subscriptions /> &nbsp;Enroll Now
            </Link>
          </Typography>
        </Paper>
        <br />
        <Paper className={classes.paper}>
          <Typography className={classes.heading} variant="h5" component="h3">
            My Previous Courses{" "}
          </Typography>
          <Grid container spacing={1} className={classes.grid}>
            <Grid
              item
              xs={12}
              sm={12}
              md={12}
              lg={12}
              xl={12}
              className={classes.courseContainer}
            >
              <Link to="/previouscourse/freelancing" className={classes.courseLink}>
                <Typography className={classes.courseName}>
                  &nbsp;&nbsp;
                  <FontAwesomeIcon icon={faGlobe} /> &nbsp; Freelancing
                </Typography>
              </Link>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
        <Paper className={classes.paper}>
          <Typography className={classes.heading} variant="h5" component="h3">
            Notice Board
          </Typography>
          {/* <Grid item xs={6} sm={6} md={6} lg={6} xl={6}>
          <FontAwesomeIcon icon={faNewspaper} className={classes.news} />
          <Typography component="p" className={classes.news}>
            News &amp; Events
          </Typography>
        </Grid> */}
          <Grid container spacing={2} className={classes.grid}>
            <Grid
              item
              xs={12}
              sm={12}
              md={6}
              lg={6}
              xl={6}
              className={classes.hover}
            >
              <Link to="/news" className={classes.news}>
                <FontAwesomeIcon icon={faNewspaper} />
                <Typography component="p">News &amp; Events</Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={12}
              md={6}
              lg={6}
              xl={6}
              className={classes.hover}
            >
              <Link to="/links" className={classes.news}>
                <FontAwesomeIcon icon={faLink} />
                <Typography component="p">Important Links</Typography>
              </Link>
            </Grid>
          </Grid>
        </Paper>
        <br />
        <Paper className={classes.paper}>
          <Typography className={classes.heading} variant="h5" component="h3">
            My Activities
          </Typography>
          <Grid container spacing={1} className={classes.grid}>
            <Grid
              item
              xs={12}
              sm={6}
              md={3}
              lg={3}
              xl={3}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <FontAwesomeIcon icon={faBookReader} />
                <Typography component="p">Exercises</Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={6}
              md={3}
              lg={3}
              xl={3}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <FontAwesomeIcon icon={faQuestion} />
                <Typography component="p">Quizes</Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={6}
              md={3}
              lg={3}
              xl={3}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <FontAwesomeIcon icon={faCommentDots} />
                <Typography component="p">Discussions</Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={6}
              md={3}
              lg={3}
              xl={3}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <FontAwesomeIcon icon={faLink} />
                <Typography component="p">My Notes</Typography>
              </Link>
            </Grid>
          </Grid>
        </Paper>
        <br />
        <Paper className={classes.paper}>
          <Typography className={classes.heading} variant="h5" component="h3">
            Trainee Services
          </Typography>
          <Grid container spacing={1} className={classes.grid}>
            <Grid
              item
              xs={12}
              sm={6}
              md={6}
              lg={6}
              xl={6}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <FontAwesomeIcon icon={faPollH} />
                <Typography>My Result</Typography>
              </Link>
            </Grid>
            <Grid
              item
              xs={12}
              sm={6}
              md={6}
              lg={6}
              xl={6}
              className={classes.hover}
            >
              <Link to="/" className={classes.news}>
                <Timeline />
                <Typography component="p">Progress Timeline</Typography>
              </Link>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </Grid>
    </DrawerView>
  );
}

export default withRouter(Dashboard);
