import React from "react";
import AdminDrawer from "./AdminDrawer";
import { withRouter, Link } from "react-router-dom";
import { getAllCategory, getInstructorByCompanyId, addCourseMain } from "../../Actions/adminActions"
import { connect } from "react-redux"
import "./AddCourse.css";
// import EditorDesc from "./Editor";
import Loader from "../../Loader"
import isEmpty from "../Protected/validation/is-empty"
class AdminCourse extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      courseImage: null,
      courseImageURL: null,
      desc: "",
      descPlaceholder: "Insert your course description",
      introduction: "",
      introductionPlaceholder: "Insert your course description",
      whatwillyoulearn: "",
      whatwillyoulearnPlaceholder: "Seperate by commas",
      instructor: "",
      videomedium: 'youtube',
      batchDuration: 0,
      batchDurationPlaceholder: "Batch duration",
      level: "",
      category: "",
      language: "",
      price: 0,
      pricePlaceholder: "Insert your course price",
      blank: "",
      fileName: "",
      errors: {},
      addcoursemain: "",
      loading: false
    };
  }

  componentDidMount() {
    this.props.getAllCategory();
    this.props.getInstructorByCompanyId();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.addcoursemain) {
      this.setState({
        addcoursemain: nextProps.addcoursemain
      });
    }
  }

  handleImage = event => {
    if (event.target.files && event.target.files[0]) {
      this.setState({
        courseImage: URL.createObjectURL(event.target.files[0]),
        courseImageURL: event.target.files[0],
        fileName: event.target.files[0].name
      });
    }
  }

  handleEditor = desc => {
    this.setState({ desc });
  };

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onClick = (e) => {
    e.preventDefault();
    const errors = {};

    if (isEmpty(this.state.introduction)) {
      errors.introduction = "Introduction/Title is Required";
    }
    if (isEmpty(this.state.desc)) {
      errors.desc = "Description is Required";
    }
    if (isEmpty(this.state.whatwillyoulearn)) {
      errors.whatwillyoulearn = "This field is Required";
    }
    if (isEmpty(this.state.category)) {
      errors.category = "Category field is Required";
    }
    if (isEmpty(this.state.instructor)) {
      errors.instructor = "Instructor field is Required";
    }
    if (isEmpty(this.state.price) || this.state.price === 0) {
      errors.price = "Price field is Required";
    }
    if (isEmpty(this.state.batchDuration) || this.state.batchDuration === 0) {
      errors.batchDuration = "Batch Duration is Required";
    }

    this.setState({ errors });
    if (isEmpty(errors)) {

      const values = {
        InstructorId: this.state.instructor,
        CourseDescription: this.state.desc,
        CourseIntroduction: this.state.introduction,
        VideosDuration: "12:25",
        batchDuration: this.state.batchDuration,
        CourseFee: this.state.price,
        VideoMedium: this.state.videomedium,
        whatwillyoulearn: this.state.whatwillyoulearn,
        TopicId: 1,
        CategoryId: this.state.category,
        courseImage: this.state.courseImageURL,
      }
      this.setState({ loading: true })
      this.props.addCourseMain(values, this.props.history);
    }
  }

  submitButton = () => {
    if(this.state.addcoursemain === 400){
      alert("Failed to add course")
    }

    if(this.state.addcoursemain === 500){
      alert("Failed to add picture")
      this.props.history.push("/adashboard")
    }

    return <button className="btn btn-success btn-lg" onClick={this.onClick}>Submit</button>
  }

  render() {
    return (
      isEmpty(this.props.getallcategory) || isEmpty(this.props.getinstructorbycompanyid) ? <Loader /> :
        <AdminDrawer>
          {
            this.props.getinstructorbycompanyid === 404 ? 
            <Link to="/addinstructor" className="text-center" style={{ padding: 20 }}><h2>Add Instructor before adding course</h2></Link> :
            <div>
          <nav className="container containerAddCourse">
            <div className="nav nav-tabs" id="nav-tab" role="tablist">
              <a
                className="nav-item nav-link active"
                id="nav-home-tab"
                data-toggle="tab"
                href="#nav-home"
                role="tab"
                aria-controls="nav-home"
                aria-selected="true"
              >
                Add Course
            </a>
            </div>
          </nav>
          <div className="tab-content containerAddCourse profileContainer mb-4" id="nav-tabContent">
            <div
              className="tab-pane fade show active"
              id="nav-home"
              role="tabpanel"
              aria-labelledby="nav-home-tab"
            >
              <div className="container pt-4">
                <div>
                  <label className="labels pb-1">
                    <span className="imp">*&nbsp;</span>Course Image
                </label>
                  <div className="row mb-4">
                    <div className="col-md-4">
                      <img
                        src={
                          this.state.courseImage
                            ? this.state.courseImage
                            : `https://via.placeholder.com/360`
                        }
                        alt="..."
                        className="img-thumbnail rounded"
                      />
                    </div>
                    <div className="col-md-8 alignBottom">
                      <p className="py-2">Make your course stand out with a great image from our
                      design team based on your preferences and style. Get your
                      free image. If you create your image, it must meet our
                      course image quality standards to be accepted. Important
                      guidelines: 750x422 pixels; .jpg, .jpeg,. gif, or .png. no
                    text on the image.</p>
                      <div className="custom-file py-2">
                        <input
                          type="file"
                          className="custom-file-input"
                          id="inputGroupFile01"
                          name="file"
                          onChange={this.handleImage}
                          value={this.state.file}
                        />
                        <label
                          className="custom-file-label customLabel"
                          htmlFor="inputGroupFile01"
                        >
                          { this.state.fileName ? `${this.state.fileName.slice(0,5)}...${this.state.fileName.slice(this.state.fileName.length-4)}` : "Choose image" }
                      </label>
                      </div>
                    </div>
                  </div>
                </div>

                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Course Introduction
                  </label>
                  <div className="input-group">
                    <input
                      type="text"
                      name="introduction"
                      value={this.state.introduction}
                      className="form-control"
                      onChange={this.handleInputChange}
                      maxLength={80}
                      placeholder={this.state.introductionPlaceholder}
                    />
                    <div className="input-group-append">
                      <span className="input-group-text">
                        {this.state.introduction.length < 80
                          ? 80 - this.state.introduction.length
                          : 0}
                      </span>
                    </div>
                  </div>
                </div>
                {this.state.errors.introduction && (
                  <div className="imp textSize">
                    {this.state.errors.introduction}
                  </div>
                )}

                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Course Instructor
                </label>
                  <div className="input-group">
                    <select
                      name="instructor"
                      value={this.state.instructor}
                      className="form-control inputBasicInfo"
                      onChange={this.handleInputChange}
                      maxLength={60}
                    >
                      <option value="">Select Instructor</option>
                      {
                        this.props.getinstructorbycompanyid.map((instructor) => (
                          <option key={instructor.instructorid} value={instructor.instructorid}>{instructor.instructorName}</option>
                        ))
                      }
                    </select>
                  </div>
                </div>
                {this.state.errors.instructor ? (
                  <div className="imp textSize">
                    {this.state.errors.instructor}
                  </div>
                ) : null}
                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Course description
                </label>
                  {/* <EditorDesc
                    handleEditor={this.handleEditor}
                    placeholder={this.state.descPlaceholder}
                  /> */}
                  <textarea
                    type="text"
                    name="desc"
                    value={this.state.desc}
                    className="form-control"
                    onChange={this.handleInputChange}
                    placeholder={this.state.descPlaceholder}
                  />
                </div>
                {this.state.errors.desc ? (
                  <div className="imp textSize">
                    {this.state.errors.desc}
                  </div>
                ) : null}
                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>What will student learn in this course
                </label>
                  <div className="input-group">
                    <textarea
                      type="text"
                      name="whatwillyoulearn"
                      value={this.state.whatwillyoulearn}
                      className="form-control"
                      onChange={this.handleInputChange}
                      maxLength={80}
                      placeholder={this.state.whatwillyoulearnPlaceholder}
                    />
                  </div>
                </div>
                {this.state.errors.whatwillyoulearn ? (
                  <div className="imp textSize">
                    {this.state.errors.whatwillyoulearn}
                  </div>
                ) : null}
                <div className="row mt-4">
                  <div className="col-md-4">
                    <label className="labels pb-1">
                      <span className="imp">*&nbsp;</span>Basic info
                  </label>
                    <input
                      type="number"
                      name="batchDuration"
                      value={this.state.batchDuration}
                      className="form-control"
                      onChange={this.handleInputChange}
                      placeholder={this.state.batchDurationPlaceholder}
                    />
                   {this.state.errors.category ? (
                      <div className="imp textSize">
                        {this.state.errors.category}
                      </div>
                    ) : null}
                  </div>

                  <div className="col-md-4 marginBasicInfo">
                    <select
                      name="level"
                      value={this.state.level}
                      className="form-control inputBasicInfo"
                      onChange={this.handleInputChange}
                      maxLength={60}
                    >
                      <option value="">Select Level</option>
                      <option defaultValue value="beginner">
                        Beginner
                    </option>
                      <option value="intermediate">Intermediate</option>
                      <option value="expert">Expert</option>
                      <option value="alllevel">All Levels</option>
                    </select>
                  </div>

                  <div className="col-md-4 marginBasicInfo">
                    <select
                      name="category"
                      value={this.state.category}
                      className="form-control inputBasicInfo"
                      onChange={this.handleInputChange}
                      maxLength={60}
                    >
                      <option value="">Select Category</option>
                      {
                        this.props.getallcategory.map((category) => (
                          <option key={category.Cata_id} value={category.Cata_id}>{category.Cata_Dsc}</option>
                        ))
                      }
                    </select>
                  </div>
                </div>

                <div className="mb-4">
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Course Pricing
                </label>
                  <div className="input-group">
                    <input
                      type="number"
                      name="price"
                      value={this.state.price}
                      className="form-control"
                      onChange={this.handleInputChange}
                      maxLength={120}
                      placeholder={this.state.pricePlaceholder}
                    />
                  </div>
                  {this.state.errors.price ? (
                    <div className="imp textSize">
                      {this.state.errors.price}
                    </div>
                  ) : null}
                </div>
                {
                  this.state.loading ?
                    this.state.addcoursemain === 500 || this.state.addcoursemain === 400 ?
                    this.submitButton() :
                    <div className="text-left py-4">
                      <div className="spinner-border" role="status">
                        <span className="sr-only">Loading...</span>
                      </div>
                    </div> :
                    <button className="btn btn-success btn-lg" onClick={this.onClick}>Submit</button>
                }
              </div>
            </div>
            <div
              className="tab-pane fade"
              id="nav-profile"
              role="tabpanel"
              aria-labelledby="nav-profile-tab"
            >
              <div className="container">...</div>
            </div>
            <div
              className="tab-pane fade"
              id="nav-contact"
              role="tabpanel"
              aria-labelledby="nav-contact-tab"
            >
              <div className="container">...</div>
            </div>
          </div>
          </div>
          }
        </AdminDrawer>
    );
  }
}

const mapStateToProps = state => ({
  getallcategory: state.admin.getallcategory,
  getinstructorbycompanyid: state.admin.getinstructorbycompanyid,
  addcoursemain: state.admin.addcoursemain
});
export default withRouter(
  connect(
    mapStateToProps,
    { getAllCategory, getInstructorByCompanyId, addCourseMain }
  )(AdminCourse)
);
