import React, { useState, useEffect } from "react";
import { KeyboardDatePicker } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import { Link } from "react-router-dom";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import BackgroundImage from "../assets/background.jpg";
import { withRouter } from "react-router-dom";
import {
  getQualification,
  getAllCountries,
  getAllCityByCountry,
  saveUser,
  getAllCompanies
} from "../Actions/authActions";
import { connect } from "react-redux";
import isEmpty from "./Protected/validation/is-empty";
import Loader from "../Loader";

const useStyles = makeStyles(theme => ({
  root: {
    height: "100vh"
  },
  image: {
    backgroundImage: `url(${BackgroundImage})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center"
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  error: {
    color: "red"
  }
}));

function Signup(props) {
  const [selectedDate, handleDateChange] = useState();
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);

  const [values, setValues] = React.useState({
    firstname: "",
    middlename: "",
    lastname: "",
    email: "",
    cnic: "",
    institute: "",
    mailingaddress: "",
    country: "",
    city: "",
    qualification: "",
    password: "",
    gender: ""
  });

  const {
    getQualification,
    getAllCountries,
    getAllCityByCountry,
    saveUser,
    getAllCompanies
  } = props;

  useEffect(() => {
    getQualification();
    getAllCountries();
    getAllCompanies()
  }, []);

  const handleChange = name => event => {
    setErrors({});
    setValues({ ...values, [name]: event.target.value });
  };
  const handleChangeCountry = name => event => {
    setErrors({});
    setValues({ ...values, [name]: event.target.value });
    if (event.target.value) {
      getAllCityByCountry(event.target.value);
    }
  };

  const submitProfile = e => {
    e.preventDefault();
    const errors = {};
    if (isEmpty(values.firstname)) {
      errors.firstname = "First Name is Required";
    }

    if (isEmpty(values.middlename)) {
      errors.middlename = "Middle Name is Required";
    }

    if (isEmpty(values.lastname)) {
      errors.lastname = "Last Name is Required";
    }

    if (!selectedDate) {
      errors.dateofbirth = "Date of Birth is Required";
    }

    if (isEmpty(values.cnic)) {
      errors.cnic = "CNIC is Required";
    }
    if (isEmpty(values.institute)) {
      errors.institute = "Institute is Required";
    }

    if (isEmpty(values.gender)) {
      errors.gender = "Gender is Required";
    }

    if (isEmpty(values.country)) {
      errors.country = "Country is Required";
    }

    if (isEmpty(values.city)) {
      errors.city = "City is Required";
    }

    if (isEmpty(values.mailingaddress)) {
      errors.mailingaddress = "Mailing Address is Required";
    }

    if (isEmpty(values.password)) {
      errors.password = "Password is Required";
    }

    if (isEmpty(values.qualification)) {
      errors.qualification = "Qualification is Required";
    }

    // if (!/^((\+92)|(0092)){0,1}\d{3}-{0,1}\d{7}$|^\d{11}$|^\d{4}\d{7}$/.test(values.mobile)) {
    //   errors.mobile = "Enter a valid mobile number"
    // }

    if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(values.email)) {
      errors.email = "Enter a valid email";
    }

    if (!/^[0-9+]{5}[0-9+]{7}[0-9]{1}$/.test(values.cnic)) {
      errors.cnic = "Enter a valid CNIC";
    }

    setErrors(errors);

    if (isEmpty(errors)) {
      setLoading(true);
      saveUser(values, selectedDate, props.history);
    }
  };

  const classes = useStyles();
  return isEmpty(props.getqualification) || isEmpty(props.getallcountries) || isEmpty(props.getallcompanies) ? (
    <Loader />
  ) : (
      <Grid container component="main" className={classes.root}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign up
          </Typography>
            {props.errorLogin === 404 ? (
              <Typography className={classes.error} component="h6" variant="h5">
                Please Enter Correct Details
            </Typography>
            ) : null}
            <form className={classes.form} noValidate>
              <TextField
                error={!isEmpty(errors.firstname) ? true : false}
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="firstname"
                label={errors.firstname ? errors.firstname : "First Name"}
                name="firstname"
                onChange={handleChange("firstname")}
                autoComplete="firstname"
                autoFocus
              />
              <TextField
                error={!isEmpty(errors.middlename) ? true : false}
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="middlename"
                helperText={errors.middlename ? errors.middlename : ""}
                label="Middle Name"
                onChange={handleChange("middlename")}
                name="middlename"
                autoComplete="middlename"
              />
              <TextField
                error={!isEmpty(errors.lastname) ? true : false}
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="lastname"
                onChange={handleChange("lastname")}
                label="Last Name"
                helperText={errors.lastname ? errors.lastname : ""}
                name="lastname"
                autoComplete="lastname"
              />
              <TextField
                id="standard-select-institute"
                select
                className={classes.textField}
                variant="outlined"
                error={!isEmpty(errors.institute) ? true : false}
                margin="normal"
                onChange={handleChange("institute")}
                name="institute"
                fullWidth
                SelectProps={{
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }}
                helperText={
                  errors.gender ? errors.gender : "Please select your institute"
                }
              >
                <option value="" defaultValue>
                  Select Your Institute
              </option>
                {props.getallcompanies.map(option => (
                  <option key={option.CompId} value={option.CompId}>
                    {option.CompNm}
                  </option>
                ))}
              </TextField>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  autoOk
                  variant="outlined"
                  error={!isEmpty(errors.dateofbirth) ? true : false}
                  inputVariant="outlined"
                  margin="normal"
                  name="dateofbirth"
                  fullWidth
                  label="Date of birth"
                  helperText={errors.dateofbirth ? errors.dateofbirth : ""}
                  format="MMM/dd/yyyy"
                  value={selectedDate}
                  // InputAdornmentProps={{ position: "start" }}
                  onChange={date => handleDateChange(date)}
                />
              </MuiPickersUtilsProvider>
              <TextField
                variant="outlined"
                margin="normal"
                error={!isEmpty(errors.mailingaddress) ? true : false}
                multiline
                required
                fullWidth
                id="mailingaddress"
                onChange={handleChange("mailingaddress")}
                label="Mailing Address"
                helperText={errors.mailingaddress ? errors.mailingaddress : ""}
                name="mailingaddress"
                autoComplete="mailingaddress"
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                error={!isEmpty(errors.cnic) ? true : false}
                id="cnic"
                onChange={handleChange("cnic")}
                label="CNIC"
                helperText={errors.cnic ? errors.cnic : ""}
                name="cnic"
                autoComplete="cnic"
              />
              <TextField
                id="standard-select-gender"
                select
                className={classes.textField}
                variant="outlined"
                error={!isEmpty(errors.gender) ? true : false}
                margin="normal"
                onChange={handleChange("gender")}
                name="gender"
                fullWidth
                // value={values.currency}
                // onChange={handleChange("currency")}
                SelectProps={{
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }}
                helperText={
                  errors.gender ? errors.gender : "Please select your gender"
                }
              >
                <option value="" defaultValue>
                  Select Your Gender
              </option>
                {[
                  {
                    value: "M",
                    label: "Male"
                  },
                  {
                    value: "F",
                    label: "Female"
                  }
                ].map(option => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </TextField>
              <TextField
                id="standard-select-country"
                select
                className={classes.textField}
                variant="outlined"
                name="country"
                error={!isEmpty(errors.country) ? true : false}
                margin="normal"
                onChange={handleChangeCountry("country")}
                fullWidth
                // value={values.currency}
                // onChange={handleChange("currency")}
                SelectProps={{
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }}
                helperText={
                  errors.country ? errors.country : "Please select your country"
                }
              >
                <option value="">Select Country..</option>
                {props.getallcountries.map(option => (
                  <option key={option.countryid} value={option.countryid}>
                    {option.countryname}
                  </option>
                ))}
              </TextField>
              <TextField
                disabled={
                  isEmpty(props.getallcitybycountry) || !values.country
                    ? true
                    : false
                }
                id="standard-select-city"
                select
                name="city"
                error={!isEmpty(errors.city) ? true : false}
                className={classes.textField}
                variant="outlined"
                onChange={handleChange("city")}
                margin="normal"
                fullWidth
                // value={values.currency}
                // onChange={handleChange("currency")}
                SelectProps={{
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }}
                helperText={
                  errors.city
                    ? errors.city
                    : isEmpty(props.getallcitybycountry)
                      ? "Select Country First"
                      : "Please select your city"
                }
                value={!values.country ? "" : values.city}
              >
                <option defaultValue={values.country === ""} value="">
                  Select City..
              </option>
                {isEmpty(props.getallcitybycountry)
                  ? null
                  : props.getallcitybycountry.map(option => (
                    <option key={option.cityid} value={option.cityid}>
                      {option.cityname}
                    </option>
                  ))}
              </TextField>
              <TextField
                id="standard-select-qualification"
                select
                className={classes.textField}
                variant="outlined"
                error={!isEmpty(errors.qualification) ? true : false}
                margin="normal"
                name="qualification"
                onChange={handleChange("qualification")}
                fullWidth
                // value={values.currency}
                SelectProps={{
                  native: true,
                  MenuProps: {
                    className: classes.menu
                  }
                }}
                helperText={
                  errors.qualification
                    ? errors.qualification
                    : "Please select your highest qualification"
                }
              >
                <option value="">Select Qualification..</option>
                {props.getqualification.map(option => (
                  <option key={option.QulId} value={option.QulId}>
                    {option.QulDsc}
                  </option>
                ))}
              </TextField>
              <TextField
                variant="outlined"
                margin="normal"
                required
                error={!isEmpty(errors.email) ? true : false}
                fullWidth
                onChange={handleChange("email")}
                id="email"
                label="Email Address"
                helperText={errors.email ? errors.email : ""}
                name="email"
                autoComplete="email"
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                error={!isEmpty(errors.password) ? true : false}
                fullWidth
                onChange={handleChange("password")}
                name="password"
                label="Password"
                helperText={errors.password ? errors.password : ""}
                type="password"
                id="password"
                type="password"
                autoComplete="current-password"
              />
              {loading ? (
                props.errorSignup === 404 ? (
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={submitProfile}
                  >
                    Sign up
                </Button>
                ) : (
                    <div className="text-center py-4">
                      <div className="spinner-border" role="status">
                        <span className="sr-only">Loading...</span>
                      </div>
                    </div>
                  )
              ) : (
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={submitProfile}
                  >
                    Sign up
              </Button>
                )}
              <Grid container>
                <Grid item xs>
                  <Link to="/forgotpassword" variant="body2">
                    Forgot password?
                </Link>
                </Grid>
                <Grid item>
                  <Link to="/signin" variant="body2">
                    {"Already Register? Sign In"}
                  </Link>
                </Grid>
              </Grid>
            </form>
          </div>
        </Grid>
      </Grid>
    );
}

const mapStateToProps = state => ({
  getqualification: state.root.getqualification,
  getallcountries: state.root.getallcountries,
  getallcitybycountry: state.root.getallcitybycountry,
  saveuser: state.root.saveuser,
  getallcompanies: state.root.getallcompanies,
  errorSignup: state.errors
});
export default withRouter(
  connect(
    mapStateToProps,
    { getQualification, getAllCountries, getAllCityByCountry, saveUser, getAllCompanies }
  )(Signup)
);
