import axios from "axios";
import { url } from "../url";
import CryptoJS from "crypto-js";
import { decryptId } from "../utils";
export const getAllUsersByCompany = () => dispatch => {
  axios
    .get(
      `${url}GetCompany?CompId=${decryptId()}`)
    .then(res => {
      dispatch({
        type: "GET_ALL_USERS_BY_COMPANY",
        payload: res.data
      });
    })
    .catch(err => console.log(err));
}
