export const saveUser = (values, selectedDate, history) => dispatch => {
  const monthNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];

  let dateofbirth = `${selectedDate.getDate()}-${
    monthNames[selectedDate.getMonth()]
    }-${selectedDate.getFullYear()}`;
  axios
    .post(
      `${url}SaveUser?firstname=${values.firstname}&middlename=${
      values.middlename
      }&lastname=${values.lastname}&EmailAddr=${values.email}&mailaddr=${
      values.mailingaddress
      }&countryid=${parseInt(values.country)}&cityid=${parseInt(values.city)}&qualificationid=${
      parseInt(values.qualification)
      }&futurequalificationid=2&dob=${dateofbirth}&cnic=${
      values.cnic
      }&companyid=${parseInt(values.institute)}&pwd=${values.password}&gender=${values.gender}`
    )
    .then(res => {
      history.push("/signin");
      dispatch({
        type: "SAVE_USER",
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: "GET_ERRORS",
        payload: err.response.status
      })
    });
};
export const login = (values, history) => dispatch => {
  axios
    .get(`${url}Login?EmailAddress=${values.email}&UserPwd=${values.password}`)
    .then(res => {

      let ciphertext = CryptoJS.AES.encrypt(`${res.data[0].emailAddr}${res.data[0].Pwd}`, "Pneumonoultramicroscopicsilicovolcanoconiosis");
      let userid = CryptoJS.AES.encrypt(`${res.data[0].userid}`, "Pneumonoultramicroscopicsilicovolcanoconiosis");
      let CompId = CryptoJS.AES.encrypt(`${res.data[0].CompId}`, "Pneumonoultramicroscopicsilicovolcanoconiosis");

      localStorage.setItem(
        "token",
        ciphertext.toString()
      );
      localStorage.setItem(
        "userid",
        userid.toString()
      );
      localStorage.setItem(
        "CompId",
        CompId.toString()
      );
      history.push("/");
      dispatch({
        type: "LOGIN",
        payload: res.data
      });
    })
    .catch(res => {
      dispatch({
        type: "GET_ERRORS",
        payload: res.response.status
      });
    });
};
