import React from "react";
import DrawerView from "./DrawerView";
import { withRouter } from "react-router-dom";
import "./Profile.css";
import isEmpty from "./validation/is-empty";
import { connect } from "react-redux";
import { getAllUsersByCompany } from "../../Actions/authActions";

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      file: "",
      firstname: "",
      middlename: "",
      lastname: "",
      day: "",
      month: "",
      year: "",
      cnic: "",
      cnicstatus: "",
      gender: "",
      country: "",
      city: "",
      mailingaddress: "",
      mobile: "",
      email: "",
      highestqualification: "",
      errors: {}
    };
  }

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  componentDidMount() {
    this.props.getAllUsersByCompany();
  }

  submitProfile = () => {
    const errors = {};

    if (isEmpty(this.state.firstname)) {
      errors.firstname = "First Name is Required";
    }

    if (isEmpty(this.state.middlename)) {
      errors.middlename = "Middle Name is Required";
    }

    if (isEmpty(this.state.lastname)) {
      errors.lastname = "Last Name is Required";
    }

    if (
      isEmpty(this.state.day) ||
      isEmpty(this.state.month) ||
      isEmpty(this.state.year)
    ) {
      errors.dateofbirth = "Date of Birth is Required";
    }

    if (isEmpty(this.state.cnic)) {
      errors.cnic = "CNIC is Required";
    }

    if (isEmpty(this.state.cnicstatus)) {
      errors.cnicstatus = "CNIC Status is Required";
    }

    if (isEmpty(this.state.gender)) {
      errors.gender = "Gender is Required";
    }

    if (isEmpty(this.state.country)) {
      errors.country = "Country is Required";
    }

    if (isEmpty(this.state.city)) {
      errors.city = "City is Required";
    }

    if (isEmpty(this.state.mailingaddress)) {
      errors.mailingaddress = "Mailing Address is Required";
    }

    if (isEmpty(this.state.highestqualification)) {
      errors.highestqualification = "Qualification is Required";
    }

    if (
      !/^((\+92)|(0092))-{0,1}\d{3}-{0,1}\d{7}$|^\d{11}$|^\d{4}-\d{7}$/.test(
        this.state.mobile
      )
    ) {
      errors.mobile = "Enter a valid mobile number";
    }

    if (
      /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(this.state.email)
      // /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.state.email)
    ) {
      errors.email = "Enter a valid email";
    }

    if (/^[0-9+]{5}-[0-9+]{7}-[0-9]{1}$/.test(this.state.cnic)) {
      errors.cnic = "Enter a valid CNIC";
    }

    if (!isEmpty(errors)) {
      this.setState({ errors });
    }
  };

  render() {
    console.log(this.props.getallusersbycompany)
    return (
      <DrawerView>
        <div>
          <h2 className="pl-5 pb-2">Sign Up</h2>
          <div className="container profileContainer">
            <div className="row">
              <div className="col-sm-12 col-md-3 text-center">
                <img
                  src="https://via.placeholder.com/200"
                  alt="..."
                  className="img-thumbnail rounded"
                />
                <div className="custom-file customFile">
                  <input
                    type="file"
                    className="custom-file-input"
                    id="inputGroupFile01"
                    name="file"
                    onChange={this.handleInputChange}
                    value={this.state.file}
                  />
                  <label
                    className="custom-file-label customLabel"
                    htmlFor="inputGroupFile01"
                  >
                    Choose file
                  </label>
                </div>
              </div>
              <div className="col-sm-12 col-md-9 nameRow">
                <div className="row">
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>First Name
                    </label>
                    <input
                      type="text"
                      name="firstname"
                      className="form-control formName"
                      onChange={this.handleInputChange}
                      value={this.state.firstname}
                    />
                    {this.state.errors.firstname ? (
                      <div className="imp textSize">
                        {this.state.errors.firstname}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Middle Name
                    </label>
                    <input
                      type="text"
                      name="middlename"
                      className="form-control formName"
                      onChange={this.handleInputChange}
                      value={this.state.middlename}
                    />
                    {this.state.errors.middlename ? (
                      <div className="imp textSize">
                        {this.state.errors.middlename}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Last Name
                    </label>
                    <input
                      type="text"
                      name="lastname"
                      className="form-control formName"
                      onChange={this.handleInputChange}
                      value={this.state.lastname}
                    />
                    {this.state.errors.lastname ? (
                      <div className="imp textSize">
                        {this.state.errors.lastname}
                      </div>
                    ) : null}
                  </div>
                </div>
                <br />
                <div className="row">
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Date of Birth
                    </label>
                    <select
                      className="custom-select formName"
                      name="day"
                      onChange={this.handleInputChange}
                      value={this.state.day}
                    >
                      <option defaultValue>Day...</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                      <option value="9">9</option>
                      <option value="10">10</option>
                      <option value="11">11</option>
                      <option value="12">12</option>
                      <option value="13">13</option>
                      <option value="14">14</option>
                      <option value="15">15</option>
                      <option value="16">16</option>
                      <option value="17">17</option>
                      <option value="18">18</option>
                      <option value="19">19</option>
                      <option value="20">20</option>
                      <option value="21">21</option>
                      <option value="22">22</option>
                      <option value="23">23</option>
                      <option value="24">24</option>
                      <option value="25">25</option>
                      <option value="26">26</option>
                      <option value="27">27</option>
                      <option value="28">28</option>
                      <option value="29">29</option>
                      <option value="30">30</option>
                      <option value="31">31</option>
                    </select>
                    {this.state.errors.dateofbirth ? (
                      <div className="imp textSize">
                        {this.state.errors.dateofbirth}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">&nbsp;</span>
                    </label>
                    <select
                      className="custom-select"
                      name="month"
                      onChange={this.handleInputChange}
                      value={this.state.month}
                    >
                      <option defaultValue>Month...</option>
                      <option value="January">January</option>
                      <option value="February">February</option>
                      <option value="March">March</option>
                      <option value="April">April</option>
                      <option value="May">May</option>
                      <option value="June">June</option>
                      <option value="July">July</option>
                      <option value="August">August</option>
                      <option value="September">September</option>
                      <option value="October">October</option>
                      <option value="November">November</option>
                      <option value="December">December</option>
                    </select>
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">&nbsp;</span>
                    </label>
                    <select
                      className="custom-select"
                      name="year"
                      onChange={this.handleInputChange}
                      value={this.state.year}
                    >
                      <option defaultValue>Year...</option>
                      <option value="1995">1995</option>
                      <option value="1996">1996</option>
                      <option value="1997">1997</option>
                      <option value="1998">1998</option>
                      <option value="1999">1999</option>
                      <option value="2001">2001</option>
                      <option value="2002">2002</option>
                      <option value="2003">2003</option>
                      <option value="2004">2004</option>
                      <option value="2005">2005</option>
                      <option value="2006">2006</option>
                      <option value="2007">2007</option>
                      <option value="2008">2008</option>
                      <option value="2009">2009</option>
                      <option value="2010">2010</option>
                      <option value="2011">2011</option>
                      <option value="2012">2012</option>
                      <option value="2013">2013</option>
                      <option value="2014">2014</option>
                      <option value="2015">2015</option>
                    </select>
                  </div>
                </div>
              </div>

              <div className="col-sm-12 col-md-9 nameRow cnic">
                <div className="row">
                  <div className="col-md-8 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>CNIC{" "}
                      <span className="notice">
                        Example: 33201-2222222-2 (With Dashes). In case you do
                        not have your own CNIC, please enter your
                        parents/guardian CNIC here
                      </span>
                    </label>
                    <input
                      type="text"
                      name="cnic"
                      className="form-control formName"
                      onChange={this.handleInputChange}
                      value={this.state.cnic}
                      placeholder="33201-2222222-2"
                    />
                    {this.state.errors.cnic ? (
                      <div className="imp textSize">
                        {this.state.errors.cnic}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12 cnicstatus">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>CNIC status
                    </label>
                    <select
                      className="custom-select formName"
                      name="cnicstatus"
                      onChange={this.handleInputChange}
                      value={this.state.cnicstatus}
                    >
                      <option defaultValue value="personal">
                        Choose..
                      </option>
                      <option value="personal">Personal</option>
                      <option value="parents/guardian">Parents/Guardian</option>
                    </select>
                    {this.state.errors.cnicstatus ? (
                      <div className="imp textSize">
                        {this.state.errors.cnicstatus}
                      </div>
                    ) : null}
                  </div>
                </div>

                <br />

                <div className="row">
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Gender
                    </label>
                    <select
                      className="custom-select formName"
                      name="gender"
                      onChange={this.handleInputChange}
                      value={this.state.gender}
                    >
                      <option defaultValue value="Pakistan">
                        Male
                      </option>
                      <option value="Pakistan">Female</option>
                    </select>
                    {this.state.errors.gender ? (
                      <div className="imp textSize">
                        {this.state.errors.gender}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Country
                    </label>
                    <select
                      className="custom-select formName"
                      name="country"
                      onChange={this.handleInputChange}
                      value={this.state.country}
                    >
                      <option defaultValue>Choose Country..</option>
                      <option value="Pakistan">Pakistan</option>
                    </select>
                    {this.state.errors.country ? (
                      <div className="imp textSize">
                        {this.state.errors.country}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-md-4 col-sm-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>City
                    </label>
                    <select
                      className="custom-select"
                      name="city"
                      onChange={this.handleInputChange}
                      value={this.state.city}
                    >
                      <option defaultValue>Choose City..</option>
                      <option value="Karachi">Karachi</option>
                    </select>
                    {this.state.errors.city ? (
                      <div className="imp textSize">
                        {this.state.errors.city}
                      </div>
                    ) : null}
                  </div>
                </div>
                <br />
                <div className="row">
                  <div className="col-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Mailing Address
                    </label>
                    <textarea
                      className="form-control"
                      name="mailingaddress"
                      aria-label="With textarea"
                      onChange={this.handleInputChange}
                      value={this.state.mailingaddress}
                    />
                    {this.state.errors.mailingaddress ? (
                      <div className="imp textSize">
                        {this.state.errors.mailingaddress}
                      </div>
                    ) : null}
                  </div>
                </div>
                <br />
                <div className="row">
                  <div className="col-sm-12 col-md-5">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Mobile
                    </label>
                    <div className="pl-2 notice">
                      Example: 92333-1122334 Country Code + Mobile Operator Code
                      + Number.
                    </div>
                    <input
                      className="form-control"
                      name="mobile"
                      onChange={this.handleInputChange}
                      value={this.state.mobile}
                      placeholder="0333-1122334"
                    />
                    {this.state.errors.mobile ? (
                      <div className="imp textSize">
                        {this.state.errors.mobile}
                      </div>
                    ) : null}
                  </div>
                  <div className="col-sm-12 col-md-7">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Email
                    </label>
                    <div className="pl-2 notice">
                      Example: abc@hotmail.com. Please enter valid email
                      address, so that we are be able to contact with you via
                      email.
                    </div>
                    <input
                      className="form-control"
                      name="email"
                      onChange={this.handleInputChange}
                      value={this.state.email}
                    />
                    {this.state.errors.email ? (
                      <div className="imp textSize">
                        {this.state.errors.email}
                      </div>
                    ) : null}
                  </div>
                </div>
                <br />
                <div className="row">
                  <div className="col-12">
                    <label className="labels">
                      <span className="imp">*&nbsp;</span>Highest Qualification
                    </label>
                    <select
                      className="custom-select formName"
                      name="highestqualification"
                      onChange={this.handleInputChange}
                      value={this.state.highestqualification}
                    >
                      <option defaultValue>Select Qualification..</option>
                      <option value="Middle">Middle</option>
                      <option value="Matric/O-level">Matric/O-level</option>
                      <option value="Intermediate">Intermediate</option>
                      <option value="Graduation">Graduation</option>
                      <option value="Masters">Masters</option>
                      <option value="MS/M.Phil">MS/M.Phil</option>
                      <option value="Ph.D">Ph.D</option>
                    </select>
                    {this.state.errors.highestqualification ? (
                      <div className="imp textSize">
                        {this.state.errors.highestqualification}
                      </div>
                    ) : null}
                  </div>
                </div>
                <br />
                <button
                  type="button"
                  className="btn btn-success"
                  onClick={this.submitProfile}
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </div>
      </DrawerView>
    );
  }
}

const mapStateToProps = state => ({
  getallusersbycompany: state.root.getallusersbycompany
});
export default withRouter(
  connect(mapStateToProps,{ getAllUsersByCompany }
  )(Profile)
);
