import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import NoteAdd from "@material-ui/icons/NoteAdd";
import List from "@material-ui/icons/List";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { getInstructorByCompanyId } from "../../Actions/adminActions";
import { connect } from "react-redux";
import Loader from "../../Loader";
import isEmpty from "../Protected/validation/is-empty";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserGraduate } from "@fortawesome/free-solid-svg-icons";
import AdminDrawer from "./AdminDrawer";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBar: {
    backgroundColor: "white",
    color: "#031926",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    backgroundColor: "white",
    color: "#031926",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap"
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    marginLeft: -100,
    padding: theme.spacing(3)
  },
  list: {
    backgroundColor: "#19469C",
    height: "100%"
  },
  listItem: {
    backgroundColor: "#19469C"
  },
  text: {
    color: "white"
  },
  paper: {
    padding: theme.spacing(0, 0)
  },
  heading: {
    backgroundColor: "#468189",
    color: "#E0FBFC",
    padding: 10
  },
  subicon: {
    backgroundColor: "#468189",
    padding: 10,
    fontSize: 20,
    textAlign: "center",
    color: "white"
  },
  subiconText: {
    textDecoration: "none",
    color: "white",
    "&:hover": {
      color: "#77ACA2",
      textDecoration: "none"
    }
  },
  typography: {
    padding: 20
  },
  news: {
    fontSize: 20,
    color: "#031926",
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  grid: {
    padding: 10,
    textAlign: "center"
  },
  hover: {
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  courseContainer: {
    backgroundColor: "#F8F6F6",
    textAlign: "left",
    marginBottom: 10
  },
  courseName: {
    fontSize: 30,
    [theme.breakpoints.down("sm")]: {
      fontSize: 16
    },
    textDecoration: "none"
  },
  courseLink: {
    color: "#031926",
    "&:hover": {
      color: "#468189",
      textDecoration: "none",
      textShadow: "5px 1px 12px #468189"
    }
  },
  container: {
    padding: 30
  }
}));

function AdminDashboard(props) {
  const classes = useStyles();

  useEffect(() => {
    props.getInstructorByCompanyId();
  }, []);

  return isEmpty(props.getinstructorbycompanyid) ? (
    <Loader />
  ) : (
    <AdminDrawer>
      <Grid container spacing={2} className={classes.container}>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Paper className={classes.paper}>
            <Link to="/courses" className={classes.subiconText}>
              <Typography className={classes.subicon} component="p">
                <List /> &nbsp;List of Courses
              </Typography>
            </Link>
          </Paper>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Paper className={classes.paper}>
            <Link to="/addcourse" className={classes.subiconText}>
              <Typography className={classes.subicon} component="p">
                <NoteAdd /> &nbsp;Add Course
              </Typography>
            </Link>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Paper className={classes.paper}>
            <Typography className={classes.heading} variant="h5" component="h3">
              Update Current Instructors{" "}
            </Typography>
            <Grid container spacing={1} className={classes.grid}>
              {props.getinstructorbycompanyid === 404 ? <h2>No Instructor</h2> :props.getinstructorbycompanyid.map(instructor => (
                <Grid
                  key={instructor.instructorid}
                  item
                  xs={12}
                  sm={12}
                  md={12}
                  lg={12}
                  xl={12}
                  className={classes.courseContainer}
                >
                  <Link
                    to={`/instructor/${instructor.instructorid}`}
                    className={classes.courseLink}
                  >
                    <Typography className={classes.courseName}>
                      &nbsp; {instructor.instructorName}
                    </Typography>
                  </Link>
                </Grid>
              ))}
            </Grid>
            <Typography className={classes.subicon} component="p">
              <Link to="/addinstructor" className={classes.subiconText}>
                <FontAwesomeIcon icon={faUserGraduate} /> &nbsp;Add Instructor
              </Link>
            </Typography>
          </Paper>
        </Grid>

        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Paper className={classes.paper}>
            <Link to="/students" className={classes.subiconText}>
              <Typography className={classes.subicon} component="p">
                <List /> &nbsp;List of Students
              </Typography>
            </Link>
          </Paper>
        </Grid>
      </Grid>
    </AdminDrawer>
  );
}

const mapStateToProps = state => ({
  getinstructorbycompanyid: state.admin.getinstructorbycompanyid
});
export default withRouter(
  connect(
    mapStateToProps,
    { getInstructorByCompanyId }
  )(AdminDashboard)
);
