import axios from "axios";
import { url } from "../url";
import { decryptId } from "../utils";
import $ from "jquery";
export const addCourseDetails = (values, setState) => dispatch => {
  axios
    .post(
      `${url}AddCourseDetails?CourseId=${values.courseid}&VideoTitle=${
        values.videoTitle
      }&VideoLink=${values.videoLink}&VideoDuration=${parseInt(values.videoDuration)}:00&WeekId=${parseInt(values.videoWeek)}`
    )
    .then(res => {
      $("#addModal").modal("hide");
      setState({
        loadingAdd: false,
        videoTitle: "",
        videoLink: "",
        videoDuration: "",
        videoWeek: ""
      });
      dispatch({
        type: "ADD_COURSE_DETAILS",
        payload: false
      });
    })
    .catch(err => {
      dispatch({
        type: "ADD_COURSE_DETAILS",
        payload: {
          payload: false,
          status: 500
        }
      });
    });
};
