export const saveInstructor = (name, desc, image, history) => dispatch => {
  axios
    .post(
      `${url}SaveInstructor?InstructorName=${name}&Description=${desc}&CompanyId=${decryptId()}`
    )
    .then(res => {
      let instructorid = res.data.slice(-1)[0].instructorid;
      // postFile(courseid, instructorid, res.data, values.courseImage)

      var bodyFormData = new FormData();
      bodyFormData.append("", image);
      axios
        .post(
          `${url}PostFile?CompanyId=${decryptId()}&CourseId=0&InstructorId=${parseInt(
            instructorid
          )}`,
          bodyFormData,
          {
            headers: {
              "Content-Type": `multipart/form-data`
            }
          }
        )
        .then(resFinal => {
          console.log("saved");
          history.push("/adashboard");
          dispatch({
            type: "SAVE_INSTRUCTOR",
            payload: res.data
          });
        })
        .catch(err => console.log(err.response.data));
    })
    .catch(err => console.log(err));
};

