import React from "react";
import AdminDrawer from "./AdminDrawer";
import { withRouter } from "react-router-dom";
import { saveInstructor } from "../../Actions/adminActions";
import { connect } from "react-redux";
import "./AddCourse.css";
// import EditorDesc from "./Editor";
// import Loader from "../../Loader";
import isEmpty from "../Protected/validation/is-empty";
import "./Instructor.css";
class AddInstructor extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      courseImage: null,
      courseImageURL: null,
      name: "",
      fileName: "",
      desc: "",
      errors: {},
      loading: false
    };
  }

  handleImage = event => {
    if (event.target.files && event.target.files[0]) {
      this.setState({
        courseImage: URL.createObjectURL(event.target.files[0]),
        courseImageURL: event.target.files[0],
        fileName: event.target.files[0].name
      });
    }
  };

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onClick = e => {
    e.preventDefault();
    const errors = {};

    if (isEmpty(this.state.name)) {
      errors.name = "Name is Required";
    }

    if (isEmpty(this.state.desc)) {
      errors.desc = "Description is Required";
    }

    this.setState({ errors });
    if (isEmpty(errors)) {
      this.setState({ loading: true });
      this.props.saveInstructor(this.state.name, this.state.desc, this.state.courseImageURL ,this.props.history);
    }
  };

  render() {
    return (
      <AdminDrawer>
        <nav className="container containerAddCourse">
          <div className="nav nav-tabs" id="nav-tab" role="tablist">
            <a
              className="nav-item nav-link active"
              id="nav-home-tab"
              data-toggle="tab"
              href="#nav-home"
              role="tab"
              aria-controls="nav-home"
              aria-selected="true"
            >
              <strong>Instructor</strong>
            </a>
          </div>
        </nav>
        <div
          className="tab-content containerAddCourse profileContainer mb-4"
          id="nav-tabContent"
        >
          <div
            className="tab-pane fade show active"
            id="nav-home"
            role="tabpanel"
            aria-labelledby="nav-home-tab"
          >
            <div className="container pt-4">
              <img
                src={
                  this.state.courseImage
                    ? this.state.courseImage
                    : `https://via.placeholder.com/360`
                }
                alt="..."
                className="img-thumbnail rounded mx-auto d-block centerBastard"
              />
              <br />
              <div className="custom-file">
                <input
                  type="file"
                  className="custom-file-input"
                  id="inputGroupFile01"
                  name="file"
                  onChange={this.handleImage}
                />
                <label
                  className="custom-file-label customLabel"
                  htmlFor="inputGroupFile01"
                >
                  { this.state.fileName ? `${this.state.fileName.slice(0,5)}...${this.state.fileName.slice(this.state.fileName.length-4)}` : "Choose image" }
                </label>
              </div>

              <div>
                <label className="labels pt-4">
                  <span className="imp">*&nbsp;</span>Instructor Name
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    name="name"
                    value={this.state.name}
                    className="form-control"
                    onChange={this.handleInputChange}
                    maxLength={80}
                    placeholder="Name"
                  />
                  <div className="input-group-append">
                    <span className="input-group-text">
                      {this.state.name.length < 80
                        ? 80 - this.state.name.length
                        : 0}
                    </span>
                  </div>
                </div>
              </div>
              {this.state.errors.name && (
                <div className="imp textSize">{this.state.errors.name}</div>
              )}

              <div>
                <label className="labels pt-4">
                  <span className="imp">*&nbsp;</span>Description
                </label>
                <div className="input-group">
                  <textarea
                    name="desc"
                    value={this.state.desc}
                    className="form-control"
                    onChange={this.handleInputChange}
                    placeholder="Description"
                  />
                </div>
              </div>
              {this.state.errors.desc && (
                <div className="imp textSize">{this.state.errors.desc}</div>
              )}
              <br />
              {this.state.loading ? (
                  <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
              ) : (
                <button
                  className="btn btn-success btn-lg"
                  onClick={this.onClick}
                >
                  Add
                </button>
              )}
            </div>
          </div>
        </div>
      </AdminDrawer>
    );
  }
}

const mapStateToProps = state => ({
  saveinstructor: state.admin.saveinstructor
});
export default withRouter(
  connect(
    mapStateToProps,
    { saveInstructor }
  )(AddInstructor)
);
