import React from "react";
import DrawerView from "./DrawerView";
import { withRouter, Link } from "react-router-dom";
import "./CourseEnrollment.css";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { withStyles } from "@material-ui/core/styles";
import Checkbox from "@material-ui/core/Checkbox";
import _ from "lodash";

const GreenCheckbox = withStyles({
  root: {
    color: "#468189",
    "&$checked": {
      color: "#468189"
    }
  },
  checked: {}
})(props => <Checkbox color="default" {...props} />);

const courses = [
  {
    courseid: 1,
    coursetitle: "AutoCAD",
    duration: "12 Weeks",
    trainer: [
      {
        instructor: "Syed Muhammad Arif"
      }
    ]
  },
  {
    courseid: 2,
    coursetitle: "Creative Writing",
    duration: "12 Weeks",
    trainer: [
      {
        instructor: "Amena Aly Kamaal"
      },
      {
        instructor: "Ayesha Fazlur Rahman"
      }
    ]
  },
  {
    courseid: 3,
    coursetitle: "Freelancing",
    duration: "12 Weeks",
    trainer: [
      {
        instructor: "Hisham Sarwar"
      }
    ]
  }
];

class CourseEnrollment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  handleChange = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  handleSubmit = () => {
    let availableCourses = this.state;
    let selectedCouses = [];
    for (let key in availableCourses) {
        if(availableCourses[key] === true){
          selectedCouses.push(key)
        }
    }

    if(selectedCouses.length > 2){
      alert("Sorry! you can not enroll more than 02 courses in a batch.")
    }
  };

  render() {
    return (
      <DrawerView>
        <div className="container">
          <h2>Course Enrollment</h2>
          <br />
          <div className="enrollContainer">
            <h3 className="enrollHeading">Available Courses</h3>
            <table className="table">
              <thead>
                <tr>
                  <th scope="col">Course Title</th>
                  <th scope="col">Duration</th>
                  <th scope="col">Trainer</th>
                  <th scope="col" />
                </tr>
              </thead>
              <tbody>
                {courses.map((course, index) => (
                  <tr key={index}>
                    <th>
                      <FormControlLabel
                        control={
                          <GreenCheckbox
                            checked={
                              this.state[
                                _.toLower(
                                  course.coursetitle.replace(/\s+/g, "")
                                )
                              ] || false
                            }
                            onChange={this.handleChange(
                              _.toLower(course.coursetitle.replace(/\s+/g, ""))
                            )}
                            value={`${_.toLower(
                              course.coursetitle.replace(/\s+/g, "")
                            )}`}
                          />
                        }
                        label={course.coursetitle}
                      />
                    </th>
                    <td>{course.duration}</td>
                    <td>
                      {course.trainer.map((t, index) => (
                        <Link
                          to={`instructor/${_.toLower(
                            t.instructor.replace(/\s+/g, "")
                          )}`}
                          key={index}
                        >
                          <div>{t.instructor}</div>
                        </Link>
                      ))}
                    </td>
                    <td>
                      <Link to={`/coursedetail/${course.courseid}`}>
                        View Detail
                      </Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            <button className="btn btn-success buttonSave ml-3 mb-2" onClick={this.handleSubmit}>
              Save
            </button>
          </div>
        </div>
      </DrawerView>
    );
  }
}

export default withRouter(CourseEnrollment);
