import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import Dashboard from "../Components/Protected/Dashboard";

// Admin
import AdminSignin from "../Components/Admin/AdminSignin";
import AdminDashboard from "../Components/Admin/AdminDashboard";
import AddCourse from "../Components/Admin/AddCourse";
import StudentsList from "../Components/Admin/StudentsList";
import AddInstructor from "../Components/Admin/AddInstructor";
import Instructor from "../Components/Admin/Instructor";
import Courses from "../Components/Admin/Courses";

// End
import Signin from "../Components/Signin";
import Signup from "../Components/Signup";
import ForgotPassword from "../Components/ForgotPassword";
import CourseViewPrevious from "../Components/Protected/CourseViewPrevious";
import CourseViewPresent from "../Components/Protected/CourseViewPresent";
import CourseEnrollment from "../Components/Protected/CourseEnrollment";
import CourseDetail from "../Components/Protected/CourseDetail";
import Profile from "../Components/Protected/Profile";
import NewsEvents from "../Components/Protected/NewsEvents";
import ImportantLinks from "../Components/Protected/ImportantLinks";
import AdminPrivateRoute from './AdminPrivateRoute';
import AdminPublicRoute from './AdminPublicRoute';
import Unknown from '../404';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';

class AppRouter extends React.Component {
  render() {
    return (
        <HashRouter>
          <Switch>
            {/* Admin */}
            <AdminPublicRoute restricted={true} exact={true} path="/admin" component={AdminSignin} />
            <AdminPrivateRoute restricted={true} userType={'admin'} exact={true} path="/adashboard" component={AdminDashboard} />
            <AdminPrivateRoute restricted={true} userType={'admin'} exact={true} path="/addcourse" component={AddCourse} />
            <AdminPrivateRoute restricted={true} userType={'admin'} exact={true} path="/students" component={StudentsList} />
            <AdminPrivateRoute restricted={true} userType={'admin'} exact={true} path="/addinstructor" component={AddInstructor} />
            <AdminPrivateRoute restricted={true} userType={'admin'} exact={true} path="/courses" component={Courses} />
            <AdminPrivateRoute exact={true} userType={'admin'} path="/instructor/:name" component={Instructor} />
            <PublicRoute exact={true} path="/404" component={Unknown}/>
            {/* Student */}
            <PublicRoute restricted={true} exact={true} path="/signin" component={Signin} />
            <PublicRoute restricted={true} exact={true} path="/signup" component={Signup} />
            <Route exact={true} path="/forgotpassword" component={ForgotPassword} />
            <PrivateRoute restricted={true} userType={'student'} path="/" exact={true} component={Dashboard} />
            <PrivateRoute exact={true} userType={'student'} path="/previouscourse/:name" component={CourseViewPrevious} />
            <PrivateRoute exact={true} userType={'student'} path="/coursedetail/:id" component={CourseDetail} />
            <PrivateRoute exact={true} userType={'student'} path="/course/:name" component={CourseViewPresent} />
            <PrivateRoute exact={true} userType={'student'} path="/enroll" component={CourseEnrollment} />
            <PrivateRoute exact={true} userType={'student'} path="/profile" component={Profile} />
            <PrivateRoute exact={true} userType={'student'} path="/news" component={NewsEvents} />
            <PrivateRoute exact={true} userType={'student'} path="/links" component={ImportantLinks} />
          </Switch>
        </HashRouter>
    );
  }
}

export default AppRouter;
